      this.isShow = !this.isShow
    }
  },
  created() {
    const instance = new Mask()
    console.log(instance, 666)
    document.body.appendChild(instance())
  },
  watch: {
    isShow(newValue) {
      if (newValue) {
        // this.$refs
      }
    }
  }
}



